@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, 0.8);
        transition: 1s all;
        &_active {
            left: 0; }
        &_item {
            margin: 10px 0 0 0;
            padding-right: 0;
            border: 0;
            padding: 5px 0;
            width: 100%;
            text-align: center; }
        &_link {
            font-size: 20px;
            display: block; } }

    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            margin: 0 0 2px 0;
            height: 2px;
            width: 100%;
            background-color: white;
            transition: 1s all; }
        &_active {
            span {
                margin: 0 0 -2px 0;
                &:first-child {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:last-child {
                    transform: translateY(3px) rotate(45deg); } } } }

    .xs-hidden {
        display: none; }

    .xs-visible {
        display: block; }

    .subheader {
        &_phone {
            display: block;
            margin: 10px auto 0 auto;
            color: white;
            font-size: 16px;
            text-transform: uppercase;
            text-align: center; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 10px auto 0 auto; } }

    .promo {
        &_header {
            line-height: 45px; } }

    .title {
        margin-top: 26px;
        font-size: 26px;
        line-height: 26px; }

    .subtitle {
        margin-top: 16px;
        font-size: 14px; }

    .mobile {
        min-height: 864px;
        padding: 48px 0 79px 0;
        &_item {
            width: 227px;
            height: 199px;
            padding-top: 65px;
            &_subtitle {
                color: #ffffff;
                font-size: 18px;
                line-height: 22px; }
            &_plus {
                bottom: 25px; } } }

    .choice {
        min-height: 424px;
        padding: 27px 0 65px 0;
        .title {
            font-size: 18px;
            line-height: 18px; }
        &_img {
            width: 67%;
            margin: 30px auto 0 auto; }
        &_descr {
            font-size: 14px;
            line-height: 18px; }
        .black {
            height: 236px; } }

    .world {
        min-height: 408px;
        padding: 29px 0;
        &_link {
            bottom: 29px; } }

    footer {
        padding: 25px 23px 20px 23px; }

    .footer {
        &_wrapper {
            display: flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            align-items: center; }
        &_wrapper > div {
            width: 100%;
            justify-content: center;
            align-items: flex-start; }
        &_social {
            width: 100%;
            display: flex;
            justify-content: center;
            padding: 20px 0 0 0; }
        &_links {
            width: 280px;
            margin: 0 auto;
            &_main {
                padding: 0;
                a {
                    font-size: 14px; } }
            &_sub {
                flex-flow: row wrap;
                align-content: flex-end;
                padding: 0 23px; }
            &_lang {
                margin-top: 13px; } }
        &_mobile {
                padding: 20px;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
